//increment the likes
export function increment(index) {
    return {
        type: 'INCREMENT_LIKES',
        index: index
    }
}
//add comment
export function addComment(postId, author, comment) {
    console.log("dispatched");

    return {
        type: 'ADD_COMMENT',
        postId,
        author,
        comment
    }
}
//remove comment

export function removeComment(postId, i) {
    return {
        type: 'REMOVE_COMMENT',
        postId,
        i
    }
}


